<template>
    <div class='typora-export os-windows a'>
  <div id='write' class=''>
    <h1><a name="重学前端笔记--css排版" class="md-header-anchor"></a><span>《重学前端》笔记--CSS排版</span></h1>
    <p><span>本文为winter发布在极客时间的《重学前端》的学习笔记及一些补充。</span></p>
    <p><span>大家支持正版喔：</span><a
        href='https://time.geekbang.org/column/article/85745'><span>time.geekbang.org/column/arti…</span></a></p>
    <h1><a name="导语" class="md-header-anchor"></a><span>导语</span></h1>
    <p><span>现在CSS提供了很多种排版方式，我们有很多选项可以选择自己适合的那一种，然而，正常流却是我们绕不开的一种排版。</span></p>
    <p><span>如果我们从严苛的CSS标准角度去理解正常流，规定排版的算法，就需要引入上述那些复杂的概念。但是，如果我们单纯地从感性认知的层面去理解正常流，它其实是简单的。</span></p>
    <h1><a name="盒模型box-model）" class="md-header-anchor"></a><span>盒模型（Box Model）</span></h1>
    <p><span>在讲解CSS排版前，需要了解下CSS盒模型。</span></p>
    <blockquote>
      <p><span>当对一个文档进行布局（lay out）的时候，浏览器的渲染引擎会根据标准之一的 CSS 基础框盒模型（CSS basic box model），将所有元素表示为一个个矩形的盒子（box）。CSS
          决定这些盒子的大小、位置以及属性（例如颜色、背景、边框尺寸…）。</span></p>
    </blockquote>
    <p><span>CSS盒模型本质上是一个盒子，封装周围的 HTML
        元素，包括外边距（</span><code>marign</code><span>），边框（</span><code>border</code><span>），内边距（</span><code>padding</code><span>），内容（</span><code>content</code><span>）。</span>
    </p>
    <p><img
        src="https://p9-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/b2e27fa3043a45bbba1059e757f184de~tplv-k3u1fbpfcp-watermark.image"
        referrerpolicy="no-referrer" alt="img"></p>
    <p><span>CSS盒模型有两种，W3C的标准盒模型和IE 盒模型（怪异盒模型）：</span></p>
    <ul>
      <li><span>W3C
          标准盒模型：属性</span><code>width</code><span>和</span><code>height</code><span>只包含</span><code>content</code><span>，不包括</span><code>border</code><span>和</span><code>padding</code>
      </li>
      <li><span>IE
          盒模型：属性</span><code>width</code><span>和</span><code>height</code><span>包含</span><code>border</code><span>和</span><code>padding</code><span>，指的是</span><code>content</code><span>
          + </span><code>padding</code><span> + </span><code>border</code></li>
    </ul>
    <p><span>css3新增的</span><code>box-sizing</code><span>属性用于切换盒模型：</span></p>
    <ul>
      <li><span>content-box 默认值，表示标准盒模型</span></li>
      <li><span>border-box，表示IE盒模型</span></li>
    </ul>
    <h1><a name="正常流" class="md-header-anchor"></a><span>正常流</span></h1>
    <h2><a name="正常流的行为" class="md-header-anchor"></a><span>正常流的行为</span></h2>
    <p><span>我们可以用一句话来描述正常流的排版行为，那就是：</span><strong><span>依次排列，排不下了换行</span></strong><span>。</span></p>
    <p><span>在正常流的基础上，剩下的功能就是基础的延伸：</span></p>
    <ul>
      <li><span>float</span></li>
    </ul>
    <p><span>float相关规则，使得一些盒占据了正常流需要的空间，我们可以把float理解为“</span><strong><span>文字环绕</span></strong><span>”。</span></p>
    <p><img
        src="https://p1-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/b8591c84c7da45c7bfd4cfe937fcdbf4~tplv-k3u1fbpfcp-watermark.image"
        referrerpolicy="no-referrer" alt="img"></p>
    <ul>
      <li><span>vertical-align</span></li>
    </ul>
    <p><span>vertical-align相关规则规定了如何在垂直方向对齐盒，设置了盒与文字是如何混合排版的。</span></p>
    <p><img
        src="https://p3-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/23f58048c84a4e5288ff65a2b0c54a18~tplv-k3u1fbpfcp-watermark.image"
        referrerpolicy="no-referrer" alt="img"></p>
    <ul>
      <li><span>margin</span></li>
    </ul>
    <p><span>我们可以把margin理解为“</span><strong><span>一个元素规定了自身周围至少需要的空间</span></strong><span>”。</span></p>
    <h2><a name="正常流的原理" class="md-header-anchor"></a><span>正常流的原理</span></h2>
    <p><span>在CSS标准中，规定了如何排布每一个文字或者盒的算法，这个算法依赖一个排版的“当前状态”，CSS把这个当前状态称为“格式化上下文（formatting context，即FC）”。</span></p>
    <blockquote>
      <p><span>Formatting context 是 W3C CSS2.1 规范中的一个概念。它是页面中的一块渲染区域，并且有一套渲染规则，它决定了其子元素将如何定位，以及和其他元素的关系和相互作用。</span></p>
    </blockquote>
    <p><span>我们可以认为排版过程是这样的：</span></p>
    <blockquote>
      <p><span>格式化上下文 + 盒/文字 = 位置</span>
        <span>formatting context + boxes/charater = positions</span></p>
    </blockquote>
    <p><span>而我们需要排版的盒，分为块级盒和行内盒。所以排版规则了</span><strong><span>块级格式化上下文（Block Formatting
          Contexts，即BFC）</span><span>*</span><em><span>和</span><span>*</span></em><span>行内级格式化上下文（Inline Formatting
          Contexts，即IFC）</span></strong><span>，可以简单描述如下：</span></p>
    <ul>
      <li><span>块级格式化上下文顺次排列元素：</span></li>
    </ul>
    <p><img
        src="https://p9-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/a132eefb608c4c06967d2d643606fa8b~tplv-k3u1fbpfcp-watermark.image"
        referrerpolicy="no-referrer" alt="img"></p>
    <ul>
      <li><span>行内级格式化上下文顺次排列元素：</span></li>
    </ul>
    <p><img
        src="https://p6-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/5c9dbbf480d44f3fae55bc62cbf23508~tplv-k3u1fbpfcp-watermark.image"
        referrerpolicy="no-referrer" alt="img"></p>
    <p><span>当我们要把正常流中的一个盒或者文字排版，需要处理以下场景：</span></p>
    <ol>
      <li><strong><span>当遇到块级盒</span></strong><span>：排入块级格式化上下文。</span></li>
      <li>
        <strong><span>当遇到行内级盒或者文字</span></strong><span>：首先尝试排入行内级格式化上下文，如果排不下，那么创建一个行盒，先将行盒排版（行盒是块级，由一行中所有的内联元素所组成，所以到第一种情况），行盒会创建一个行内级格式化上下文。</span>
      </li>
      <li>
        <strong><span>遇到float</span></strong><span>：把盒的顶部跟当前行内级上下文上边缘对齐，然后根据float的方向把盒的对应边缘对到块级格式化上下文的边缘，之后重排当前行盒。</span>
      </li>
    </ol>
    <p><span>当然，这仅仅是一个简单的逻辑，实际场景中CSS排版渲染会很复杂。</span></p>
    <h1><a name="块级格式化上下文bfc）" class="md-header-anchor"></a><span>块级格式化上下文（BFC）</span></h1>
    <blockquote>
      <p><span>BFC是一个独立的布局环境，其中的元素布局是不受外界的影响。</span></p>
    </blockquote>
    <h2><a name="创建方式" class="md-header-anchor"></a><span>创建方式</span></h2>
    <p><span>常见的BFC创建方式如下（更多的创建方式见</span><a
        href='https://developer.mozilla.org/zh-CN/docs/Web/Guide/CSS/Block_formatting_context'><span>这里</span></a><span>）：</span>
    </p>
    <ul>
      <li><span>根元素（</span><code>&lt;html&gt;</code><span>），这里就可以理解正常流的逻辑</span></li>
      <li><code>float</code><span>的值不为</span><code>none</code><span>。</span></li>
      <li>
        <code>overflow</code><span>的值为</span><code>auto</code><span>，</span><code>scroll</code><span>或</span><code>hidden</code><span>。</span>
      </li>
      <li><code>display</code><span>的值为</span><code>table-cell</code><span>, </span><code>table-caption</code><span>,
        </span><code>inline-block</code><span>中的任何一个。</span></li>
      <li><code>position</code><span>的值不为</span><code>relative</code><span>和</span><code>static</code><span>。</span>
      </li>
    </ul>
    <h2><a name="布局规则" class="md-header-anchor"></a><span>布局规则</span></h2>
    <ul>
      <li><span>BFC内部的块盒与行盒，会在垂直方向，一个接一个地放置（正常流）</span></li>
      <li><span>属于同一个BFC的两个相邻盒的</span><strong><span>垂直方向上的margin</span></strong><span>会发生重叠，产生</span><a
          href='https://www.w3.org/TR/CSS21/box.html#collapsing-margins'><span>折叠边距</span></a></li>
      <li><span>BFC就是页面上的一个隔离的独立容器，容器里面的子元素不会影响到外面的元素。反之也如此。</span></li>
      <li><span>BFC的区域不会与</span><code>float</code><span>元素区域重叠（可由上一条推导出）</span></li>
      <li><span>每个元素的左外边距与包含块的左边界接触（从左到右），即使浮动元素也是如此。（说明BFC中子元素不会超出他的包含块）</span></li>
      <li><span>计算BFC的高度时，浮动元素也参与计算</span></li>
    </ul>
    <h2><a name="bfc的作用" class="md-header-anchor"></a><span>BFC的作用</span></h2>
    <p><span>上面的布局规则描述可能有些地方有点抽象，下面简单描述下BFC规则的作用：</span></p>
    <ul>
      <li><span>避免外边距折叠</span></li>
    </ul>
    <pre spellcheck="false" class="md-fences md-end-block ty-contain-cm modeLoaded" lang="html"
