  <pre class=" CodeMirror-line "
    role="presentation"><span role="presentation" style="padding-right: 0.1px;">  margin: 20px 0;</span></pre>
  <pre class=" CodeMirror-line "
    role="presentation"><span role="presentation" style="padding-right: 0.1px;">  background-color: green;</span></pre>
