  </div>
  </pre>
  <p><span>根据第二条规则，这两个box会产生重叠，垂直方向上间距为</span><code>20px</code><span>（垂直距离取两个外边距的最大值）。 </span><img
      src="https://p6-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/a992cf76079e44d493d36a7ce402f687~tplv-k3u1fbpfcp-watermark.image"
      referrerpolicy="no-referrer" alt="img"></p>
  <p><span>那么，根据第三条规则，我们可以将其中一个box放在另一个BFC中，来解决这个问题（当然，解决重叠问题还有其他方案，这里只记录BFC的解决方案）。</span></p>
