  <ul>
    <li><span>清除浮动</span></li>
  </ul>
  <p><span>清除浮动，其实就是解决浮动元素的包含块高度塌陷的问题。</span></p>
  <p>
    <span>根据第五条规则，</span><code>float</code><span>元素会沿其容器的左侧或右侧放置。那么，在正常流中，</span><code>float</code><span>元素从网页的正常流动(文档流)中移除，会沿外部容器的左侧或右侧，从而导致其包含块高度塌陷。</span>
  </p>
