      referrerpolicy="no-referrer" alt="img"></p>
  <h1><a name="行内级格式化上下文ifc）" class="md-header-anchor"></a><span>行内级格式化上下文（IFC）</span></h1>
  <p><span>在一个行内格式化上下文中，盒是一个接一个</span><strong><span>水平</span></strong><span>放置的，从包含块的顶部开始。</span></p>
  <ul>
    <li>
      <span>这些盒水平方向的</span><code>margin</code><span>、</span><code>border</code><span>以及</span><code>padding</code><span>属性会起作用</span>
    </li>
    <li><span>这些盒在垂直方向上的对齐方式可以不一样：可以顶部或底部对齐，或根据其中文字的基线对齐</span></li>
    <li><span>行盒的宽度容纳不下子元素时，子元素会换到下一行显示，并且会产生新的行盒</span></li>
    <li><span>IFC中时不可能有块级元素的，当插入块级元素时（如p中插入div）会产生两个匿名块与div分隔开，即产生两个IFC，每个IFC对外表现为块级元素，与div垂直排列</span></li>
  </ul>
  <h2><a name="行盒line-box）" class="md-header-anchor"></a><span>行盒（line box）</span></h2>
  <p><span>包含同一行的盒的矩形区域我们称之为</span><strong><span>行盒（line box）</span></strong><span>。</span></p>
  <ul>
    <li><span>行盒的高度由</span><code>line-height</code><span>决定(有多个行内元素，则取</span><code>line-height</code><span>的最大值)。</span>
    </li>
  </ul>
  <p><span>行盒的高度能够容纳它包含的所有盒。当盒的高度小于行盒的高度时，盒的垂直对齐方式由</span><code>vertical-align</code><span>属性决定。</span></p>
  <p><span>这里，我们可以理解为什么存在行内元素的</span><strong><span>上下间距不生效</span></strong><span>的问题。</span></p>
  <ul>
    <li><span>行盒的宽度由它的包含盒和</span><code>float</code><span>情况来决定。</span></li>
  </ul>
  <p><span>通常，行盒的左边接触到其包含盒的左边，右边接触到其包含盒的右边。但是float元素优先排列，可能让行盒的宽度缩短。</span></p>
  <pre spellcheck="false" class="md-fences md-end-block ty-contain-cm modeLoaded"
    lang="html"><div class="CodeMirror cm-s-inner CodeMirror-wrap" lang="html"><div style="overflow: hidden; position: relative; width: 3px; height: 0px; top: 16px; left: 20px;"><textarea autocorrect="off" autocapitalize="off" spellcheck="false" tabindex="0" style="position: absolute; bottom: -1em; padding: 0px; width: 1000px; height: 1em; outline: none;"></textarea></div><div class="CodeMirror-scrollbar-filler" cm-not-content="true"></div><div class="CodeMirror-gutter-filler" cm-not-content="true"></div><div class="CodeMirror-scroll" tabindex="-1"><div class="CodeMirror-sizer" style="margin-left: 0px; margin-bottom: 0px; border-right-width: 0px; padding-right: 0px; padding-bottom: 0px;"><div style="position: relative; top: 0px;"><div class="CodeMirror-lines" role="presentation"><div role="presentation" style="position: relative; outline: none;"><div class="CodeMirror-measure"><pre><span>xxxxxxxxxx</span></pre>
  </div>
  <div class="CodeMirror-measure"></div>
