  <div class="CodeMirror-code" role="presentation" style="">
    <div class="CodeMirror-activeline" style="position: relative;">
      <div class="CodeMirror-activeline-background CodeMirror-linebackground"></div>
      <div class="CodeMirror-gutter-background CodeMirror-activeline-gutter" style="left: 0px; width: 0px;"></div>
      <pre class=" CodeMirror-line "
        role="presentation"><span role="presentation" style="padding-right: 0.1px;"><span class="cm-tag cm-bracket">&lt;</span><span class="cm-tag">span</span> <span class="cm-attribute">class</span>=<span class="cm-string">"s1"</span><span class="cm-tag cm-bracket">&gt;</span>111111<span class="cm-tag cm-bracket">&lt;/</span><span class="cm-tag">span</span><span class="cm-tag cm-bracket">&gt;</span></span></pre>
    </div>
    <pre class=" CodeMirror-line "
      role="presentation"><span role="presentation" style="padding-right: 0.1px;"><span class="cm-tag cm-bracket">&lt;</span><span class="cm-tag">span</span> <span class="cm-attribute">class</span>=<span class="cm-string">"s2"</span><span class="cm-tag cm-bracket">&gt;</span>222222<span class="cm-tag cm-bracket">&lt;/</span><span class="cm-tag">span</span><span class="cm-tag cm-bracket">&gt;</span></span></pre>
    <pre class=" CodeMirror-line "
