  <p><span>由上面的例子我们可以看出，正常情况下盒之间有间距，但是存在float的情况下，盒会紧贴float元素，从而减少行盒的宽度。</span></p>
  <p><span>并且，当一行的行内级盒的总宽度小于它们所在的行盒的宽度时，它们在行盒里的水平分布由</span><code>text-align</code><span>决定。</span></p>
  <h2><a name="ifc的作用" class="md-header-anchor"></a><span>IFC的作用</span></h2>
  <ul>
    <li><span>水平居中</span></li>
  </ul>
  <p>
    <span>当一个块元素要在环境中水平居中时，设置其为</span><code>inline-block</code><span>则会在外层产生IFC，通过</span><code>text-align</code><span>则可以使其水平居中。</span>
  </p>
  <ul>
    <li><span>垂直居中</span></li>
  </ul>
  <p><span>创建一个IFC，用其中一个元素撑开父元素的高度，然后设置其</span><code>vertical-align:middle</code><span>，其他行内元素则可以在此父元素下垂直居中。</span></p>
  <h1><a name="总结" class="md-header-anchor"></a><span>总结</span></h1>
  <p><span>每一个盒的排版布局，依赖一个排版的“当前状态”，CSS称为“格式化上下文（formatting context，即FC）”。</span></p>
  <p><span>盒分为块级盒和行内盒，所以排版规则了</span><strong><span>块级格式化上下文（Block Formatting
        Contexts，即BFC）</span><span>*</span><em><span>和</span><span>*</span></em><span>行内级格式化上下文（Inline Formatting
        Contexts，即IFC）</span></strong><span>。</span></p>
  </div>
</div>
