      font-style: italic;
    }

    .cm-link {
      text-decoration: underline;
    }

    .cm-error {
      color: red;
    }

    .cm-invalidchar {
      color: red;
    }

    .cm-constant {
      color: rgb(38, 139, 210);
    }

    .cm-defined {
      color: rgb(181, 137, 0);
    }

    div.CodeMirror span.CodeMirror-matchingbracket {
      color: rgb(0, 255, 0);
    }

    div.CodeMirror span.CodeMirror-nonmatchingbracket {
      color: rgb(255, 34, 34);
    }

    .cm-s-inner .CodeMirror-activeline-background {
      background: inherit;
    }

    .CodeMirror {
      position: relative;
      overflow: hidden;
    }

    .CodeMirror-scroll {
      height: 100%;
      outline: 0px;
      position: relative;
      box-sizing: content-box;
      background: inherit;
    }

    .CodeMirror-sizer {
      position: relative;
    }

    .CodeMirror-gutter-filler,
    .CodeMirror-hscrollbar,
    .CodeMirror-scrollbar-filler,
    .CodeMirror-vscrollbar {
      position: absolute;
      z-index: 6;
      display: none;
    }

    .CodeMirror-vscrollbar {
      right: 0px;
      top: 0px;
      overflow: hidden;
    }

    .CodeMirror-hscrollbar {
      bottom: 0px;
      left: 0px;
      overflow: hidden;
    }

    .CodeMirror-scrollbar-filler {
      right: 0px;
      bottom: 0px;
    }

    .CodeMirror-gutter-filler {
      left: 0px;
      bottom: 0px;
    }

    .CodeMirror-gutters {
      position: absolute;
      left: 0px;
      top: 0px;
      padding-bottom: 30px;
      z-index: 3;
    }

    .CodeMirror-gutter {
      white-space: normal;
      height: 100%;
      box-sizing: content-box;
      padding-bottom: 30px;
      margin-bottom: -32px;
      display: inline-block;
    }

    .CodeMirror-gutter-wrapper {
      position: absolute;
      z-index: 4;
      background: 0px 0px !important;
      border: none !important;
    }

    .CodeMirror-gutter-background {
      position: absolute;
      top: 0px;
      bottom: 0px;
      z-index: 4;
    }

    .CodeMirror-gutter-elt {
      position: absolute;
      cursor: default;
      z-index: 4;
    }

    .CodeMirror-lines {
      cursor: text;
    }

    .CodeMirror pre {
      border-radius: 0px;
      border-width: 0px;
      background: 0px 0px;
      font-family: inherit;
      font-size: inherit;
      margin: 0px;
      white-space: pre;
      overflow-wrap: normal;
      color: inherit;
      z-index: 2;
      position: relative;
      overflow: visible;
    }

    .CodeMirror-wrap pre {
      overflow-wrap: break-word;
      white-space: pre-wrap;
      word-break: normal;
    }

    .CodeMirror-code pre {
      border-right: 30px solid transparent;
      width: fit-content;
    }

    .CodeMirror-wrap .CodeMirror-code pre {
      border-right: none;
      width: auto;
    }

    .CodeMirror-linebackground {
      position: absolute;
      left: 0px;
      right: 0px;
      top: 0px;
      bottom: 0px;
      z-index: 0;
    }

    .CodeMirror-linewidget {
      position: relative;
      z-index: 2;
      overflow: auto;
    }

    .CodeMirror-wrap .CodeMirror-scroll {
      overflow-x: hidden;
    }

    .CodeMirror-measure {
      position: absolute;
      width: 100%;
      height: 0px;
      overflow: hidden;
      visibility: hidden;
    }

    .CodeMirror-measure pre {
      position: static;
    }

    .CodeMirror div.CodeMirror-cursor {
      position: absolute;
      visibility: hidden;
      border-right: none;
      width: 0px;
    }

    .CodeMirror div.CodeMirror-cursor {
      visibility: hidden;
    }

    .CodeMirror-focused div.CodeMirror-cursor {
      visibility: inherit;
    }

    .cm-searching {
      background: rgba(255, 255, 0, 0.4);
    }

    @media print {
      .CodeMirror div.CodeMirror-cursor {
        visibility: hidden;
      }
    }


