    @font-face {
      font-family: 'Source Code Pro';
      font-style: normal;
      font-weight: 400;
      src: local('Source Code Pro'), local('SourceCodePro-Regular'),
        url('file:///C://Users//%E5%BC%A0%E7%A3%8A//AppData//Roaming//Typora/themes/gitbook/SourceCodePro-Regular.ttf') format('truetype');
    }

    @import '';

    /*by H16nning*/

    :root {
      --bg-color: white;
      --side-bar-bg-color: #f5f7f9;
      --control-text-color: #3b454e;
      --primary-color: rgb(26, 168, 139);
      --primary-btn-border-color: rgb(26, 168, 139);
      --active-file-bg-color: #e6ecf1;
      --active-file-text-color: inherit;
      --active-file-border-color: rgb(26, 168, 139);
      --item-hover-text-color: #242a31;
      --item-hover-bg-color: #f5f7f9;
      --window-border: 1px solid #e6ecf1;
      --select-text-font-color: white;
      --select-text-bg-color: rgb(50, 119, 229);

      --md-char-color: #9daab6;
      --heading-char-color: #9daab6;
      --meta-content-color: rgb(26, 168, 139);

      --borders: #e6ecf1;
      --table-border-color: #e6ecf1;
      --boxes: #f5f7f9;
      --boxes-darker: #d8e0e7;
      --boxes-darker2: #bac2c9;
      --boxes-darkest: #9daab6;
      --drag-placeholder-color: #d8e0e7;

      --text-color: #3b454e;
      --heading-text-color: #242a31;
      --light-text-color: #9daab6;
      --light-text-color-darker: #74818d;
      --codeboxes: #183055;
      --codeboxes-lighter: #1c375f;
      --rawblock-edit-panel-bd: transparent;

      --primary-color-white: rgb(106, 228, 203);
      --primary-color-white-darker: rgb(67, 196, 170);
      --primary-color-darker: rgb(12, 131, 107);
      --focus-ring-color: rgb(26, 168, 139);

      --danger-color: rgb(255, 70, 66);

      --node-fill: #ececff;
      --node-border: #ccccff;
      --cluster-fill: #ffffde;
      --cluster-border: #aaaa33;
      --note-fill: #fff5ad;
      --note-border: #aaaa33;

      /*****************************/

      --font-family: 'Roboto', sans-serif;
      --code-font-family: 'Source Code Pro';
      --monospace: 'Source Code Pro';
    }

    html,
    .form-control,
    .modal {
      font-size: 16px;
    }

    body {
      background: var(--bg-color);
      font-family: var(--font-family);
      font-weight: 400;
      color: #3b454e;
      line-height: 1.6rem;
      height: 100%;
    }

    #write {
      font-size: 0.95rem;
      max-width: 850px;
      margin: 0 auto;
      padding: 30px;
      padding-bottom: 100px;
      position: static;
      width: 100%;
    }

    #write>ul:first-child,
    #write>ol:first-child {
      margin-top: 30px;
    }

    a {
      color: var(--primary-color);
      text-decoration: none !important;
      transition-duration: 0.2s;
      transition-property: color;
    }

    a:hover {
      color: var(--primary-color-darker);
    }

    .ty-preferences a {
      color: var(--primary-color);
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      position: relative;
      color: var(--heading-text-color);
      cursor: text;
    }

    h1:hover a.anchor,
    h2:hover a.anchor,
    h3:hover a.anchor,
    h4:hover a.anchor,
    h5:hover a.anchor,
    h6:hover a.anchor {
      text-decoration: none;
    }

    h1 tt,
    h1 code {
      font-size: inherit;
    }

    h2 tt,
    h2 code {
      font-size: inherit;
    }

    h3 tt,
    h3 code {
      font-size: inherit;
    }

    h4 tt,
    h4 code {
      font-size: inherit;
    }

    h5 tt,
    h5 code {
      font-size: inherit;
    }

    h6 tt,
    h6 code {
      font-size: inherit;
    }

    h1 {
      font-size: 2rem;
      font-weight: 700;
      line-height: 1.5;
      margin-top: 3rem;
      margin-bottom: 0.8rem;
      padding-bottom: 0.2rem;
      border-bottom: solid 2px var(--borders);
    }

    h2 {
      font-size: 1.4rem;
      font-weight: 700;
      line-height: 1.5;
      margin-top: 2.5rem;
      margin-bottom: 0.5rem;
    }

    h3 {
      font-size: 1.2rem;
      font-weight: 700;
      line-height: 1.5;
      margin-top: 2rem;
      margin-bottom: 0.5rem;
    }

    h4 {
      font-size: 1rem;
      font-weight: 700;
      line-height: 1.5;
      margin-top: 1.5rem;
      margin-bottom: 0.5rem;
    }

    h5 {
      font-size: 0.9rem;
      font-weight: 700;
      line-height: 1.5;
      margin-top: 1.3rem;
      margin-bottom: 0.5rem;
    }

    h6 {
      font-size: 0.9rem;
      font-weight: 500;
      line-height: 1.5;
      margin-top: 1.3rem;
      margin-bottom: 0.5rem;
      opacity: 0.75;
    }

    #write>h1.md-focus:before,
    #write>h2.md-focus:before,
    #write>h3.md-focus:before,
    #write>h4.md-focus:before,
    #write>h5.md-focus:before,
    #write>h6.md-focus:before {
      color: var(--light-text-color);
      border: none;
      position: absolute;
      font-size: 1rem;
      font-weight: 700;
      padding: 0px;
      line-height: 1;
    }

    #write>h1.md-focus:before {
      content: 'h1';
      top: 1.1rem;
      left: -1.75rem;
    }

    #write>h2.md-focus:before {
      content: 'h2';
      top: 0.6rem;
      left: -1.75rem;
    }

    #write>h3.md-focus:before {
      content: 'h3';
      top: 0.4rem;
      left: -1.75rem;
    }

    #write>h4.md-focus:before {
      content: 'h4';
      top: 0.3rem;
      left: -1.75rem;
    }

    #write>h5.md-focus:before {
      content: 'h5';
      top: 0.2rem;
      left: -1.75rem;
    }

    #write>h6.md-focus:before {
      content: 'h6';
      top: 0.2rem;
      left: -1.75rem;
    }

    h1:first-child,
    h2:first-child,
    h3:first-child,
    h4:first-child,
    h5:first-child,
    h6:first-child,
    blockquote h1,
    blockquote h2,
    blockquote h3,
    blockquote h4,
    blockquote h5,
    blockquote h6 {
      margin-top: 0rem;
    }

    p,
    blockquote,
    ul,
    ol,
    dl {
      margin: 0.5rem 0rem 1.5rem 0rem;
    }

    li>ol,
    li>ul {
      margin: 0 0;
    }

    hr {
      height: 2px;
      padding: 0;
      margin: 16px 0;
      background-color: var(--borders);
      border: 0 none;
      overflow: hidden;
      box-sizing: content-box;
    }

    li p.first {
      display: inline-block;
    }

    ul,
    ol {
      padding-left: 30px;
    }

    ul:first-child,
    ol:first-child {
      margin-top: 0;
    }

    ul:last-child,
    ol:last-child {
      margin-bottom: 0;
    }

    .md-blockmeta {
      color: var(--md-char-color);
    }

    mark {
      background-color: var(--primary-color);
      color: white;
      padding: 0.2rem 0.4rem;
      border-radius: 0.2rem;
    }

    /*BLOCKQUOTE*/

    blockquote {
      width: 100%;
      background-color: var(--boxes);
      border-left: 4px solid var(--primary-color);
      border-radius: 0.3rem;
      padding: 1rem;
    }

    blockquote blockquote {
      padding-right: 0;
    }

    /*TABLE*/

    table {
      font-size: 0.875rem;
      padding: 0;
      margin: 1.5rem 0;
      word-break: initial;
    }

    table tr {
      border-top: 1px solid var(--borders);
      border-bottom: 1px solid var(--borders);
      margin: 0;
      padding: 0;
    }

    table tr.md-end-block {
      border-top: none;
    }

    table tr th {
      font-weight: bold;
      border: none;
      border-bottom: solid 2px var(--borders);
      margin: 0;
      padding: 10px 16px;
      transition-duration: 0.3s;
      transition-property: background-color;
    }

    table tr td {
      border: none;
      margin: 0;
      padding: 10px 16px;
      transition-duration: 0.3s;
      transition-property: background-color;
    }

    #write table tr td:hover,
    #write table tr th:hover {
      background-color: var(--boxes);
    }

    table tr th:first-child,
    table tr td:first-child {
      margin-top: 0;
    }

    table tr th:last-child,
    table tr td:last-child {
      margin-bottom: 0;
    }

    /*OTHER TABLE THINGS*/

    .ty-table-edit {
      background-color: var(--bg-color);
      padding: 0.3rem;
      border: solid 1px var(--borders);
      border-radius: 0.3rem;
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
      transition-duration: 0.3s;
      transition-property: opacity;
    }

    .ty-table-edit:active,
    .ty-table-edit:focus,
    .popover:active,
    .popover:focus {
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
    }

    .popover {
      border: solid 1px var(--borders);
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
      border-radius: 0.3rem;
    }

    .popover .arrow {
      border-bottom-color: var(--borders);
    }

    .md-grid-board .md-grid-ext {
      background-color: var(--boxes);
    }

    .md-grid-board tr[row='1'] .md-grid-ext {
      background-color: var(--boxes-darkest);
    }

    .md-grid-board a.md-active,
    .md-grid-board a:hover {
      background-color: var(--primary-color-white);
      border-color: var(--primary-color-white-darker);
    }

    #md-grid-width,
    #md-grid-height {
      border-radius: 0.3rem;
      border-color: var(--borders);
    }

    #md-grid-width,
    #md-grid-height {
      border-radius: 0.3rem;
      border-color: var(--borders);
      transition-duration: 0.3s;
      transition-property: border-color;
    }

    #md-grid-width:focus,
    #md-grid-height:focus {
      border-radius: 0.3rem;
      border-color: var(--primary-color);
    }

    /*CODE*/

    .CodeMirror {
      padding: 1rem;
      font-family: 'Source Code Pro', monospace;
    }

    .cm-s-inner .CodeMirror-gutters {
      background-color: none;
      border-right: none;
      border-radius: 0px;
      width: 2rem;
      color: white;
      height: 100%;
      white-space: nowrap;
