    }

    .cm-s-inner span.cm-builtin {
      color: #8ffaaa;
    }

    .md-fences.md-focus .cm-s-inner .CodeMirror-activeline-background {
      background: none;
    }

    .cm-s-inner .CodeMirror-selected,
    .cm-s-inner .CodeMirror-selectedtext {
      background-color: rgba(255, 255, 255, 0.1);
    }

    .cm-s-typora-default .cm-header,
    .cm-s-typora-default .cm-property {
      color: white;
      font-weight: 400;
    }

    #write .code-tooltip {
      box-shadow: rgba(116, 129, 141, 0.08) 0px 2px 6px 0px;
      border: solid 1px var(--borders);
      border-radius: 0.3rem;
      background-color: var(--bg-color);
      color: var(--text-color);
      width: 15rem;
      height: 2.5rem;
      bottom: -3rem;
      padding: 0.1875rem;
    }

    #write .code-tooltip .ty-input {
      border: solid 1px var(--borders);
      height: 2rem;
      line-height: 2rem;
      margin: 0rem;
      padding-left: 0.5rem !important;
      text-align: left;
      display: block;
      font-size: 0.8rem;
      font-weight: 600;
      font-family: var(--font-family);
      transition-duration: 0.3s;
      transition-property: border;
    }

    #write .code-tooltip .ty-input:focus {
      border-color: var(--primary-color);
    }

    .html-for-mac .ty-cm-lang-input:empty:after {
      left: 0.5rem;
    }

    .autoComplt-list li,
    .fences-auto-suggest li {
      font-weight: 500;
      transition-duration: 0.3s;
      transition-property: background-color, color;
    }

    .autoComplt-list li.active,
    .fences-auto-suggest li:hover {
      background-color: var(--boxes);
      color: var(--primary-color);
    }

    .md-fences,
    tt {
      border-radius: 0.3rem;
      color: #ffffff;
      padding: 1.5rem;
      font-size: 0.8rem;
    }

    code {
      padding: 0.2rem 0.4rem;
      background-color: var(--borders);
      font-size: 0.9rem;
      border-radius: 0.2rem;
    }

    .md-fences .CodeMirror div.CodeMirror-cursor,
    #typora-source div.CodeMirror-cursor {
      border-left: solid 1px white;
    }

    .md-fences {
      margin-bottom: 1.5rem;
      margin-top: 1.5rem;
      padding-top: 8px;
      padding-bottom: 6px;
      background-color: var(--codeboxes);
    }

    #typora-source {
      background-color: var(--codeboxes);
      color: white;
      font-size: 0.9rem;
    }

    /*DIAGRAMS*/

    .md-diagram-panel {
      color: var(--text-color);
      border: solid 1px var(--borders);
      border-radius: 0.3rem;
    }

    .md-diagram-panel text,
    .md-diagram-panel .label {
      color: var(--text-color);
    }

    .md-diagram-panel text {
      fill: var(--text-color) !important
        /*required*/
      ;
    }

    .md-diagram-panel-error {
      color: var(--danger-color);
    }

    /*CHECKBOXES*/

    .md-task-list-item>input:before,
    input[type='checkbox']:before {
      border-radius: 0.2rem;
      margin-top: -0.08rem;
      margin-left: -0.1rem;
      width: 1rem;
      height: 1rem;
      background-color: var(--borders);
      content: ' ';
      display: block;
      transition-duration: 0.3s;
      transition-property: background-color;
    }

    .md-task-list-item:hover>input:before,
    input[type='checkbox']:hover:before {
      background-color: var(--boxes-darker);
    }

    .md-task-list-item>input:checked:before,
    .md-task-list-item>input[checked]:before,
    input[type='checkbox']:checked:before {
      background-color: var(--primary-color);
    }

    .md-task-list-item:hover>input:checked:before,
    .md-task-list-item:hover>input[checked]:before,
    input[type='checkbox']:hover:checked:before {
      background-color: var(--primary-color-darker);
    }

    .md-task-list-item>input:after,
    .md-task-list-item>input:after,
    input[type='checkbox']:after {
      transform: rotate(-45deg);
      position: absolute;
      border: 2px solid white;
      border-top: 0;
      border-right: 0;
      top: 0.16rem;
      left: 0.1rem;
      width: 0.6rem;
      height: 0.375rem;
      content: ' ';
      opacity: 0;
      transition-duration: 0.3s;
      transition-property: opacity;
    }

    .md-task-list-item>input:checked:after,
    .md-task-list-item>input[checked]:after,
    input[type='checkbox']:checked:after {
      opacity: 1;
    }

    .ty-preferences input[type='checkbox']:before {
      width: 1.2rem;
      height: 1.2rem;
    }

    .ty-preferences input[type='checkbox']:after {
      width: 0.5rem;
      height: 0.32rem;
      top: 0.19rem;
      left: 0.14rem;
    }

    @media print {
      html {
        font-size: 13px;
      }

      table,
      pre {
        page-break-inside: avoid;
      }

      pre {
        word-wrap: break-word;
      }
    }

    #write pre.md-meta-block {
      padding: 1rem;
      line-height: 1.45;
      background-color: var(--boxes);
      border: 0;
      border-radius: 0.3rem;
      color: var(--text-color);
      border-left: solid 4px var(--boxes-darkest);
    }

    .md-image>.md-meta {
      border-radius: 3px;
      padding: 2px 0px 0px 4px;
      font-size: 0.9em;
      color: inherit;
    }

    .md-tag {
      color: var(--md-char-color);
      opacity: 1;
    }

    /*TOC*/

    .md-toc {
      margin: 1.5rem 0rem;
    }

    .md-toc:focus .md-toc-content {
      border: none;
    }

    .md-toc-tooltip .btn {
      padding: 0.5rem;
      line-height: 1rem;
    }

    .md-toc-tooltip .btn:hover {
      color: var(--primary-color) !important
        /*required*/
      ;
      background-color: var(--boxes) !important
        /*required*/
      ;
    }

    /*MATH*/

    .MathJax_SVG:focus {
      outline: none;
    }

    .md-rawblock {
      margin: 1.5rem 0rem;
    }

    .md-rawblock-container {
      transition-duration: 0.3s;
      transition-property: box-shadow, border;
      padding: 8px;
      border: solid 1px transparent;
      border-radius: 0.3rem;
    }

    .md-rawblock:hover>.md-rawblock-container {
      background-color: transparent;
      border: solid 1px var(--borders);
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
    }

    .md-math-block .md-rawblock-control:not(.md-rawblock-tooltip) {
      background-color: var(--bg-color);
      border-left: solid 2px var(--primary-color);
      border-right: solid 2px var(--primary-color);
    }

    .md-math-block .md-rawblock-before {
      border-top-left-radius: 0.3rem;
      border-top-right-radius: 0.3rem;
      border-top: solid 2px var(--primary-color);
    }

    .md-math-block .md-rawblock-after {
      border-bottom-left-radius: 0.3rem;
      border-bottom-right-radius: 0.3rem;
      border-bottom: solid 2px var(--primary-color);
      margin-bottom: 0.5rem;
    }

    #write .md-math-block .code-tooltip {
      width: 100%;
    }

    .md-mathblock-input span.cm-tag {
      color: #15726d;
    }

    .md-mathblock-input span.cm-atom {
      color: #8a1fa0;
    }

    .md-mathblock-input span.cm-number {
      color: #df4617;
    }

    .code-tooltip.md-hover-tip {
      border-radius: 0.3rem;
      background-color: var(--bg-color);
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
      color: var(--text-color);
      opacity: 1;
    }

    .md-hover-tip .code-tooltip-content {
      border: solid 1px var(--borders);
    }

    .md-hover-tip .md-arrow:after {
      background-color: var(--bg-color);
      border: solid 1px var(--borders);
      box-shadow: none;
    }

    /*HTML*/

    .md-htmlblock-panel {
      border: solid 2px var(--primary-color);
      border-radius: 0.3rem;
    }

    /*FOOTER*/

    footer.ty-footer {
      border: none;
    }

    .footer-item {
      opacity: 1 !important;
      color: var(--light-text-color);
      transition: 0.3s;
    }

    .footer-item:hover {
      color: var(--light-text-color-darker) !important;
    }

    #outline-btn:hover {
      color: var(--light-text-color-darker) !important;
    }

    /*SIDEBAR*/

    #typora-sidebar {
      border-right: solid 1px var(--borders);
    }

    .info-panel-tab {
      opacity: 1;
    }

    .info-panel-tab-border {
      color: var(--primary-color);
      border-radius: 2px;
    }

    #ty-sidebar-search-back-btn {
      margin: auto;
    }

    .ty-show-outline-filter #file-library-search,
    .ty-show-search #file-library-search {
      height: 4rem;
    }

    #file-library-search-input {
      height: 2rem;
    }

    #file-library-search-panel input {
      margin-top: 8px !important;
    }

    .ty-sidebar-search-panel .searchpanel-search-option-btn {
      top: 14px;
    }

    #typora-sidebar #filesearch-case-ion-btn,
    #typora-sidebar #filesearch-word-ion-btn {
      background: none;
      margin-top: 0.45rem;
      transition-duration: 0.2s;
      transition-property: background-color;
    }

    #typora-sidebar #filesearch-case-ion-btn:hover,
    #typora-sidebar #filesearch-word-ion-btn:hover {
      color: var(--primary-color);
    }

    /*sidebar outline*/

    #close-outline-filter-btn {
      top: 15px;
      opacity: 1;
      color: var(--light-text-color) !important;
    }

    #close-outline-filter-btn:hover {
      color: var(--primary-color) !important;
      background-color: transparent !important;
    }

    #close-outline-filter-btn:active {
      background-color: transparent !important;
    }

    #outline-content {
      padding-right: 0px;
      line-height: 1rem;
    }

    .outline-item {
      display: flex;
      padding-top: 0px;
      padding-bottom: 0px;
    }

    .outline-item .outline-label {
      display: block;
      width: 100%;
      padding-top: 0.4rem;
      padding-bottom: 0.4rem;
      border-right: solid 2px transparent;
      font-size: 0.8rem;
      font-weight: 500;
      color: var(--light-text-color-darker);
      transition-duration: 0.3s;
      transition-property: color;
    }

    .outline-item:hover {
      background: none;
    }

    .outline-item:hover .outline-label {
      color: var(--primary-color);
    }

    .outline-label:hover {
      text-decoration: none;
    }

    .outline-active.outline-label {
      border-right: solid 2px var(--primary-color);
      font-weight: 500;
      color: var(--primary-color);
    }

    .outline-expander {
      padding-top: 0.4rem;
      padding-right: 0.5rem;
    }

    /*sidebar file-list and search results*/

    #file-library-list[data-state='complete'] #sidebar-loading-template {
      padding: 0rem;
    }

    #typora-sidebar .file-list-item,
    .ty-search-item {
      border: none;
      transition-duration: 0.3s !important;
      transition-property: background-color, border, color, height !important;
      padding: 1rem;
    }

    #typora-sidebar .file-list-item:hover,
    .ty-search-item:hover {
      background: var(--borders);
    }

    #typora-sidebar .ty-search-item-line:hover,
    #typora-sidebar .ty-search-item-line.active {
      background-color: transparent;
    }

    #typora-sidebar .ty-search-item-line.active {
      color: var(--primary-color);
      font-weight: 800;
    }

    #typora-sidebar .file-list-item-file-name {
      font-weight: 800;
      font-size: 0.9rem;
      margin-bottom: 0rem;
      line-height: 1.8rem;
      float: right;
    }

    #typora-sidebar .file-list-item-file-ext-part {
      font-weight: 800;
      opacity: 0.5;
    }

    #typora-sidebar .file-list-item-parent-loc,
    #typora-sidebar .file-list-item-time {
      font-weight: 400;
      opacity: 0.5;
      display: block;
    }

    #typora-sidebar .file-list-item-summary {
      float: left;
      font-size: 0.8rem;
      opacity: 0.9;
    }

    #typora-sidebar input.file-list-item-file-name {
      margin: 0.5rem 0rem 0.5rem 0.7rem;
      padding: 0.4rem !important;
      line-height: 1rem;
      float: right;
      border-radius: 0.3rem;
      font-weight: 500;
      background-color: white !important;
    }

    #typora-sidebar .file-list-item.file-library-file-node {
      border: none;
    }

    #typora-sidebar .file-tree-node.active .file-node-background,
    #typora-sidebar .file-list-item.active,
    #typora-sidebar .ty-search-item.active {
      background-color: white;
      outline: 1px solid var(--borders);
      border: none;
      color: var(--primary-color) !important;
    }

    #typora-sidebar .file-tree-node.active .file-node-content {
      color: var(--primary-color) !important;
    }

    #typora-sidebar .file-tree-node {
      padding: 0rem;
      font-weight: 500;
      font-size: 0.9rem;
      margin-left: 0.8rem;
    }

    #typora-sidebar .file-tree-node .file-node-content {
      padding: 0rem;
      line-height: 2.2rem;
      height: 2.2rem;
      background: none;
    }

    #typora-sidebar .file-tree-node .file-node-background {
      padding: 0rem;
      height: 2.2rem;
    }

    #typora-sidebar .file-tree-node .file-node-icon {
      margin-right: 0.5rem;
    }

    #typora-sidebar .file-tree-node .file-node-icon.fa-file-text-o {
      margin-top: 0.33rem;
    }

    #typora-sidebar .file-tree-node .file-node-icon.fa-folder {
      margin-top: 0.36rem;
    }

    #typora-sidebar .file-tree-node .fa-caret-down,
    #typora-sidebar .file-tree-node .fa-caret-right {
      position: relative;
      top: 5px;
    }

    #typora-sidebar .file-tree-node .file-tree-rename-input {
      height: 2.2rem;
      background: none;
      border: none;
      font-size: 0.9rem;
      font-weight: 500;
      margin: 0rem;
      padding-left: 0rem;
    }

    #typora-sidebar .ty-search-item-collapse-icon {
      top: 9px;
    }

    /*no left border*/
    #typora-sidebar .file-tree-node.active>.file-node-background {
      border: none;
    }

    /*no dotted highlighting*/
    .file-library-node:not(.file-node-root):focus>.file-node-content {
      outline: none;
    }

    #typora-sidebar #sidebar-files-menu {
      border: solid 1px var(--borders);
      border-radius: 0.3rem;
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
    }

    #typora-sidebar #ty-sidebar-footer {
      background-color: var(--bg-color);
      border-top: solid 1px var(--borders);
      font-weight: 500;
    }

    #typora-sidebar #ty-sidebar-footer li {
      transition-duration: 0.2s;
      transition-property: background-color, color;
    }

    #typora-sidebar #ty-sidebar-footer li:hover {
      color: var(--primary-color);
      background-color: var(--boxes);
    }

    /*cursor*/
    .file-node-content:hover {
      cursor: pointer;
    }

    .file-tree-node:not(.file-node-root):not(.file-node-expanded) .file-node-background {
      transition-duration: 0.2s;
      transition-property: background-color;
    }

    .file-tree-node:not(.file-node-root):not(.file-node-expanded):hover .file-node-background {
      background-color: var(--borders);
    }

    #typora-sidebar .sidebar-footer-item {
      transition-duration: 0.2s;
      transition-property: background-color, color;
      font-weight: 700;
    }

    #typora-sidebar .sidebar-footer-item:hover {
      color: var(--primary-color);
      background-color: var(--boxes);
    }

    #typora-quick-open {
      background-color: white;
      border: 1px solid var(--borders);
      border-radius: 0.3rem;
      padding: 0rem;
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
    }

    #typora-quick-open-input {
      margin-right: 20px;
    }

    #typora-quick-open-input .input {
      box-shadow: none;
      border: none;
      margin: 0.5rem;
      margin-left: 0.8rem;
    }

    .typora-quick-open-item {
      background-color: white;
      border: none;
      font-weight: 500;
      transition-duration: 0.2s;
      transition-property: background-color;
    }

    .typora-quick-open-item:hover {
      background-color: var(--boxes);
      color: var(--primary-color);
    }

    .typora-quick-open-item.active {
      background-color: var(--boxes);
      border: none;
    }

    .ty-quick-open-category-title {
      font-size: 10px;
      font-weight: 700;
      letter-spacing: 1.2px;
      text-transform: uppercase;
      opacity: 1;
      color: var(--light-text-color);
      line-height: 32px;
      padding-top: 6px;
      height: 32px;
    }

    /** focus mode */
    .on-focus-mode blockquote {
      border-left-color: rgba(85, 85, 85, 0.12);
    }

    .md-lang {
      color: var(--primary-color);
    }

    /*NOTIFICATION*/

    #md-notification {
      border-bottom: solid 1px var(--borders);
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
    }

    #md-notifcation .btn {
      border: 0;
    }

    /*PREFERENCES*/

    .ty-preferences {
      font-family: var(--font-family);
    }

    .ty-preferences .window-header {
      justify-content: space-between;
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
      border-bottom: solid 1px var(--borders);
    }

    .ty-preferences .window-header-content {
      background-color: white;
      margin: 0rem;
    }

    .ty-preferences .window-header h2 {
      font-weight: 600;
      font-size: 1.5rem;
      margin: 0rem;
      margin-left: 1rem;
    }

    .unibody-window .ty-preferences .window-header h2 {
      margin-left: 1rem !important;
    }

    .ty-preferences .window-header-back {
      margin-left: 1.2rem;
    }

    .ty-preferences .window-header-back .icon {
      border-right: none;
    }

    /*preferences sidebar*/
    .ty-preferences .window-content {
      background-color: white;
    }

    .ty-preferences .nav-group-item {
      color: var(--text-color);
      height: 2.5rem;
      line-height: 2.6rem;
      font-weight: 500;
      padding: 0px 0px 0px 2rem;
      font-size: 1rem;
      transition-duration: 0.2s;
      transition-property: background-color;
    }

    .ty-preferences .nav-group-item:hover {
      background-color: var(--borders);
      border-radius: 0px;
    }

    .ty-preferences .nav-group-item.active {
      border-radius: 0rem;
      border: none;
      outline: 1px solid var(--borders);
      background-color: white;
      color: var(--primary-color);
    }

    .ty-preferences .pane-sm {
      background: var(--boxes);
      flex-basis: 240px;
      flex-grow: 0;
      justify-content: center;
      border-right: 1px solid var(--borders);
      margin: 0rem;
      padding: 0rem;
    }

    .ty-preferences .pane-sm .list-group {
      width: 100%;
      max-width: 30rem;
    }

    .ty-preferences .list-group-header {
      display: flex;
      justify-content: space-around;
    }

    .ty-preferences .list-group-header div {
      width: 100%;
      margin-right: 18px;
      margin-left: 18px;
    }

    .ty-preferences .pane-sm .search-input {
      margin: 0rem !important;
      width: 100%;
      font-size: 1rem !important;
      height: 2.75rem;
    }

    .ty-preferences .pane-sm .search-input:active,
    .ty-preferences .pane-sm .search-input:focus {
      border: solid 1px var(--primary-color) !important;
      outline: none;
    }

    /*preferences main*/
    .ty-preferences .panel-header {
      font-weight: 600;
      font-size: 1.6rem;
    }

    /*preferences stuff*/
    .ty-preferences .dropdown-menu>li,
    .dropdown-item {
      font-weight: 500;
      font-size: 1rem;
      transition-duration: 0.2s;
      transition-property: all;
    }

    .ty-preferences .dropdown-menu .active,
    .ty-preferences .dropdown-menu li:hover,
    .dropdown-item:hover {
      background-color: var(--boxes);
      color: var(--primary-color);
    }

    #ty-spell-check-dict-missing-secondary-btn:hover {
      color: var(--primary-color) !important;
    }

    .header-close .icon {
      border: none !important;
    }

    /*DROPDOWN*/

    .dropdown-menu:not(.megamenu-menu-list),
    .auto-suggest-container {
      background-color: var(--bg-color);
      border: solid 1px var(--borders) !important;
      box-shadow: rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;
      user-select: none;
    }

    .dropdown-menu>li>a {
      font-weight: 500;
      font-size: 0.8rem;
      transition-duration: 0.2s;
      transition-property: all;
    }

    .dropdown-menu>.active>a,
    .dropdown-menu>li>a:hover,
    .menu-style-btn.active,
    .context-menu.dropdown-menu>.active>a,
    .context-menu.dropdown-menu>li>a:hover {
      color: var(--primary-color);
      background-color: var(--boxes);
    }

    .menu-style-btn {
      color: var(--text-color);
      border: none;
      transition-duration: 0.2s;
      transition-property: all;
    }

    .menu-style-btn:hover {
      color: var(--primary-color);
      background-color: var(--boxes);
      border: none;
    }

    header .menu-style-btn:hover {
      background-color: transparent;
    }

    .menu-item-container {
      padding: 0 12px 0 4px;
    }

    .menu-item-container a.menu-style-btn {
      padding: 0.3rem 0.6rem;
      margin: 0;
    }

    .dropdown-menu .divider {
      border-color: var(--borders);
    }

    /*BUTTON*/

    .btn,
    button {
      border: none !important;
      border-radius: 0.3em !important;
      color: var(--text-color) !important;
      transition-duration: 0.2s;
      transition-property: all;
      font-size: 0.9em !important;
      font-weight: 500;
      outline: none;
    }

    .btn-default {
      border: none !important;
      border-radius: 0.3em !important;
      background-color: var(--boxes) !important;
    }

    .btn:hover,
    .button-hover {
      border: none;
      border-radius: 0.3em;
      background-color: var(--borders) !important;
      color: var(--text-color);
    }

    button:active,
    button.active,
    .btn:active,
    .btn-default:active {
      border: none;
      border-radius: 0.3em;
      background-color: var(--boxes-darker) !important;
      box-shadow: none;
      outline: none;
      color: var(--text-color);
    }

    .btn:focus {
      border: none !important;
      outline: none !important;
      background-color: var(--boxes-darker);
    }

    .btn-primary {
      border: none;
      border-radius: 0.3em;
      background-color: var(--primary-color);
      color: white !important;
    }

    .btn-primary:hover,
    .btn-primary:focus {
      color: white;
      background-color: var(--primary-color-darker) !important;
    }

    .btn.dropdown-toggle-split,
    .btn.dropdown-toggle-split:hover {
      border-radius: 0em 0.3em 0.3em 0em !important;
    }

    #ty-spell-check-dict-missing-primary-btn {
      border-radius: 0.3em 0em 0em 0.3em !important;
    }

    .open>.dropdown-toggle.btn-primary {
      background-color: var(--primary-color);
      border-color: transparent;
    }

    /*GHOST BUTTON*/

    .window-header button,
    #close-sidebar-menu-btn,
    .html-for-mac .sidebar-tab-btn,
    .label-hint,
    .ty-table-edit .btn,
    .zoom-hint-button {
      background-color: transparent !important;
      color: var(--light-text-color) !important;
      opacity: 1 !important;
      transition-duration: 0.2s;
